         url('./fonts/ubuntu-light-webfont.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'ubuntu';
    src: url('./fonts/ubuntu-medium-webfont.woff2') format('woff2'),
         url('./fonts/ubuntu-medium-webfont.woff') format('woff');
    font-weight: 500;
