}
@font-face {
    font-family: 'ubuntu';
    src: url('./fonts/ubuntu-regular-webfont.woff2') format('woff2'),
         url('./fonts/ubuntu-regular-webfont.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}
@-webkit-keyframes visible {
    0% { opacity: 0; }
    100% {opacity: 1; }
   }
@keyframes visible {
    0% { opacity: 0; }
    100% {opacity: 1; }
   }
@-webkit-keyframes loaded {
       0% { background-color: #ffdede; }
       50% {background-color: #fff;}
       100% {background-color: #ffdede; }
      }
@keyframes loaded {
       0% { background-color: #ffdede; }
       50% {background-color: #fff;}
       100% {background-color: #ffdede; }
      }
/* Reset default parameters */
*{
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
a{
	color: #000;
	text-decoration: none;
}
ul, li{
	list-style: none;
}
body{
	font: 300 11px ubuntu,Arial,sans-serif;
  background-color: #efeeee;
}
.font-ubuntu-light{
  color: #a5a4a4;
  font-size: 11px;
  font-weight: 300;
  line-height: 15px;
}
.container{
  max-width: 1250px;
  width: 100%;
  background-color: #fff;
  padding: 40px 39px 0 39px;
  margin: 0 auto;
}

.product-wrapp{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 40px;
}

.card-wrapp{
  max-width: 270px;
  width: 100%;
  text-align: center;
  border: 1px solid #e5e5e5;
  padding: 0 10px 10px;
  margin-bottom: 40px;

  -webkit-animation: visible .6s ease-in-out 1;

          animation: visible .6s ease-in-out 1;
}
.card-wrapp:hover{
  -webkit-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.35);
          box-shadow: 0 3px 7px rgba(0, 0, 0, 0.35);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.img-wrapp{
  width: 100%;
  max-width: 250px;
  height: 250px;
  border: 1px solid #e5e5e5;
  margin: 0 auto;
  margin-top: -20px;
  background-color: #fff;
  margin-bottom: 17px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.img-wrapp>a{
  -ms-flex-item-align: center;
      align-self: center;
}
.sale-product{
  position: absolute;
  -ms-flex-item-align: center;
      align-self: center;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 70px;
  color: #ffffff;
  font-size: 13px;
  font-weight: 500;
  line-height: 38px;
  text-transform: uppercase;
  letter-spacing: -0.26px;
  background-color: #ed1111;
}
.hidden{
  display: none;
}
.new-product{
  position: absolute;
  -ms-flex-item-align: center;
      align-self: center;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 70px;
  color: #ffffff;
  font-size: 13px;
  font-weight: 500;
  line-height: 38px;
  text-transform: uppercase;
  letter-spacing: -0.26px;
  background-color: #000000;
}
.title-product-cart{
    text-align: left;
}
.title-product-cart>a{
  color: #333333;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 14px;
  width: 100%;
}
.description-product-cart{
  text-align: left;
  width: 100%;
  margin-bottom: 24px;
  height: 30px;
  overflow: hidden;
}
.price-product-cart{
  display: -ms-flexbox;
  display: flex;
  text-align: left;
  margin-bottom: 28px;
}
.coast-product-cart{
  color: #333333;
  font-size: 25px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  padding-right: 23px;
}
.discount-cost-product-cart{
  color: #ed1111;
  font-size: 17px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration:line-through
}
.button-wrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.button-wrap>button{
  border: 1px solid #e5e5e5;
  outline: 0;
  width: 48%;
  line-height: 36px;
  text-transform: uppercase;
  cursor: pointer;
}
.add-to-cart-button{
  background: #fff;
  color: #ed1111;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.26px;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.add-to-cart-button:hover{
  background-color: #ed8e8e;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.view-button{
  color: #999e9f;
  font-size: 12px;
  font-weight: 300;
  background-color: #d9d9d9;
}
.view-button:hover{
  background-color: #b3b3b3;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  color: #fff;
}
.wrap-load-button{
  text-align: center;
  position: relative;
}
.wrap-load-button button{
  border: 1px solid #e5e5e5;
  outline: 0;
  width: 120px;
  text-align: center;
  line-height: 36px;
  text-transform: uppercase;
  cursor: pointer;
}
.loadButtonAnimate{
  -webkit-animation: loaded 2s ease-in-out infinite;
          animation: loaded 2s ease-in-out infinite;
}
.footer{
  padding-bottom: 92px;
  padding-top: 73px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.block-footer{
  width: 370px;
  border: 1px solid #e5e5e5;
  padding: 44px 26px 50px 26px;
}
.title-block-footer{
  color: #333333;
  font-family: Raleway;
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.description-block-footer{
  height: 45px;
  overflow: hidden;
  margin-bottom: 15px;
}
.list-block-footer ul>li i{
  line-height: 25px;
  position: relative;
  padding-left: 15px;
  font-style: normal;
}
.list-block-footer ul>li i::before {
    color: #ed1111;
    font-family: FontAwesome;
    font-size: 12px;
    text-rendering: auto;
    position: absolute;
    left: 0;
}

.contact-footer{
  margin-bottom: 25px;

}
.contact-footer i{
  position: relative;
  padding-left: 40px;
}
.contact-footer i::before{
  color: #ed1111;
  font: 31px/26px FontAwesome;
  text-rendering: auto;
  position: absolute;
  left: 0;
}
.contact-footer.contact-email i::before{
  font-size: 29px;
}
.contact-footer i>*{
  display: inline-block;
  font-family: ubuntu;
  color: #a5a4a4;
}
.contact-footer i>a:hover{
  color: #545454;
}
@media screen and (max-width: 1200px){
    .block-footer{
      padding: 44px 20px 50px 20px;
      width: 358px;
    }
    .product-wrapp{
      -ms-flex-pack: center;
          justify-content: center;
    }
    .card-wrapp{
      margin-right: 20px;
      margin-left: 20px;
    }
}

@media screen and (max-width: 1155px){
    .product-wrapp{
      -ms-flex-pack: center;
          justify-content: center;
    }
    .footer{
      -ms-flex-pack: center;
          justify-content: center;
      padding-bottom: 10px;
    }
    .block-footer{
      margin-left: 15px;
      margin-right: 15px;
      margin-bottom: 20px;

    }
    .block-footer.story-info{
      width: 100%;
      padding: 20px 20px 0px 20px;
    }
    .story-info-contact{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between;
    }
    .contact-footer{
      margin-left: 10px;
      margin-right: 10px;
    }
}
@media screen and (max-width: 855px){
  .block-footer{
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .story-info-contact{
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-pack: start;
          justify-content: start;
  }
  .contact-footer {
    margin-left: 30px;
    margin-right: 55px;
  }
}
@media screen and (max-width: 700px){
  .card-wrapp{
    margin-right: 10px;
    margin-left: 10px;
  }
}
@media screen and (max-width: 657px){
  .container{
    padding-left: 15px;
    padding-right: 15px;
  }
  .product-wrapp{
    -ms-flex-pack: center;
        justify-content: center;
  }
}

@media screen and (max-width: 610px){
  .product-wrapp {
    -ms-flex-pack: center;
        justify-content: center;
  }
  .card-wrapp {
    margin-right: 20px;
    margin-left: 20px;
  }

}
@media screen and (max-width: 580px){
  .contact-footer {
    margin-left: 30px;
    margin-right: 10px;
  }
}
@media screen and (max-width: 414px){
    .product-wrapp{
      -ms-flex-pack: center;
          justify-content: center;
      margin-bottom: 10px;
    }
    .footer{
      padding-bottom: 0px;
      padding-top: 30px;
    }
    .block-footer{
      display: none;
    }
    .block-footer.story-info{
      display: block;
    }
}

@media screen and (max-width: 400px){
  .description-block-footer{
    height: 60px;
  }
  .contact-footer {
    margin-left: 0;
    margin-right: 10px;
  }
}
@media screen and (max-width: 400px){
  .block-footer{
    display: none;
  }
  .block-footer.story-info{
    display: block;
  }
}



